header {
  position: fixed;
  z-index: 50;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  padding-top: 7px;
  background: #ffffff;
}
header:before {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 6px;
  content: "";
  background: linear-gradient(to right, #000000 0, #000000 50%, #ff0000 50%, #ff0000 100%);
}
header > .inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 1000px;
  height: 100%;
  margin: 0 auto;
}
header > .inner nav ul {
  display: flex;
}
header > .inner nav ul li {
  padding: 5px 0;
}
header > .inner nav ul li:not(:last-child) {
  margin-right: 20px;
  padding-right: 20px;
  border-right: 1px solid #404040;
}
header > .inner nav ul li a {
  font-size: 13px;
}